Grid {
    align-items: center;
}

#HackName {
    animation: fadeIn ease 2s;
    -webkit-animation: fadeIn ease 2s;
    -moz-animation: fadeIn ease 2s;
    -o-animation: fadeIn ease 2s;
    -ms-animation: fadeIn ease 2s;
    animation: HackMove 5s infinite;
}

@keyframes HackMove {
    60% {
        transform: translateY(-20px);
    }
}

@keyframes fadeIn {
    0% {
        opacity: 0;
    }
    100% {
        opacity: 1;
    }
}

@-moz-keyframes fadeIn {
    0% {
        opacity: 0;
    }
    100% {
        opacity: 1;
    }
}

@-webkit-keyframes fadeIn {
    0% {
        opacity: 0;
    }
    100% {
        opacity: 1;
    }
}

@-o-keyframes fadeIn {
    0% {
        opacity: 0;
    }
    100% {
        opacity: 1;
    }
}

@-ms-keyframes fadeIn {
    0% {
        opacity: 0;
    }
    100% {
        opacity: 1;
    }
}

.content-main {
    margin-top: 4rem;
}

.content-hack36 {
    font-family: "kgSummerSunShineBlackout";
    color: #203246;
    font-size: 4.8rem;
    margin-bottom: 1rem;
    letter-spacing: 3px;
}

.content-c3 {
    font-family: "gentona";
    color: #203246;
    font-size: 1.5rem;
    margin-bottom: 0.8rem;
}

.content-dates {
    font-family: "jura", sans-serif;
    color: #203246;
    font-size: 2.5rem;
}

.base-button,
.base-button>a {
    font-family: "jura", sans-serif;
    font-size: 2rem;
    background-color: #1e113b;
    color: azure;
    width: 300px;
    height: 60px;
    display: grid;
    align-items: center;
    border-radius: 50px;
    //	align-self: center;
}

@-webkit-keyframes swinging {
    0% {
        -webkit-transform: rotate(10deg);
    }
    50% {
        -webkit-transform: rotate(-5deg)
    }
    100% {
        -webkit-transform: rotate(10deg);
    }
}

@keyframes swinging {
    0% {
        transform: rotate(10deg);
    }
    50% {
        transform: rotate(-5deg)
    }
    100% {
        transform: rotate(10deg);
    }
}

.swingimage {
    -webkit-transform-origin: 50% 0;
    transform-origin: 50% 0;
    -webkit-animation: swinging 3s ease-in-out forwards 2.3;
    animation: swinging 3s ease-in-out forwards 2.3;
}

